"use client"

import { useMemo, useState } from 'react'
import { Header } from '@/components/header'
import { Footer } from '@/components/footer'
import { products } from '@/lib/products'
import { ProductCard } from '@/components/product-card'

type SortOption = 'featured' | 'price-low' | 'price-high' | 'newest' | 'rating'

const winterCategoryFilters = [
  { label: 'All', value: null },
  { label: 'Menswear', value: 'Men' },
  { label: 'Womenswear', value: 'Women' },
  { label: 'Kids', value: 'Kids' },
  { label: 'Sportswear', value: 'Sportswear' },
  { label: 'Accessories', value: 'Accessories' },
  { label: 'Footwear', value: 'Footwear' },
]

export default function WinterCollectionPage() {
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null)
  const [sortBy, setSortBy] = useState<SortOption>('featured')

  const winterProducts = useMemo(
    () => products.filter((product) => product.season === 'Winter'),
    [products]
  )

  const visibleProducts = useMemo(() => {
    let filtered = winterProducts

    if (selectedCategory) {
      filtered = filtered.filter((product) => product.category === selectedCategory)
    }

    switch (sortBy) {
      case 'price-low':
        filtered = [...filtered].sort((a, b) => a.price - b.price)
        break
      case 'price-high':
        filtered = [...filtered].sort((a, b) => b.price - a.price)
        break
      case 'rating':
        filtered = [...filtered].sort((a, b) => b.rating - a.rating)
        break
      case 'newest':
        filtered = [...filtered].sort((a, b) => b.id - a.id)
        break
      default:
        filtered = [...filtered]
    }

    return filtered
  }, [selectedCategory, sortBy, winterProducts])

  return (
    <>
      <Header />
      <main className="min-h-screen bg-background">
        <div className="bg-muted/50 px-4 md:px-6 py-8">
          <div className="max-w-7xl mx-auto">
            <p className="text-sm uppercase tracking-[0.3em] text-primary font-semibold mb-2">Seasonal Drop</p>
            <h1 className="text-3xl md:text-4xl font-bold mb-3">Winter Collection</h1>
            <p className="text-muted-foreground max-w-3xl">
              Thermal-engineered layers, insulating accessories, and cold-weather footwear crafted for sub-zero cityscapes.
              Each piece includes accurate pricing, discounts, ratings, and stock visibility to mimic an enterprise storefront.
            </p>
          </div>
        </div>

        <div className="max-w-7xl mx-auto px-4 md:px-6 py-12 space-y-8">
          <div className="flex flex-col lg:flex-row gap-4 items-start lg:items-center">
            <div className="flex flex-wrap gap-2">
              {winterCategoryFilters.map((filter) => (
                <button
                  key={filter.label}
                  onClick={() => setSelectedCategory(filter.value)}
                  className={`px-4 py-2 rounded-full border smooth-transition ${
                    selectedCategory === filter.value
                      ? 'bg-primary text-primary-foreground border-primary'
                      : 'border-border text-foreground hover:border-primary/60'
                  }`}
                >
                  {filter.label}
                </button>
              ))}
            </div>
            <div className="lg:ml-auto">
              <select
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value as SortOption)}
                className="px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
              >
                <option value="featured">Featured</option>
                <option value="newest">Newest</option>
                <option value="price-low">Price: Low to High</option>
                <option value="price-high">Price: High to Low</option>
                <option value="rating">Top Rated</option>
              </select>
            </div>
          </div>

          <div className="text-sm text-muted-foreground">
            Showing {visibleProducts.length} insulated essentials
          </div>

          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
            {visibleProducts.map((product) => (
              <ProductCard key={product.id} {...product} />
            ))}
          </div>
        </div>
      </main>
      <Footer />
    </>
  )
}





